.benefits {
    flex-direction: column;
    text-align: center;
    background-color: $color-background-benefits;

    img {
        width: 15rem;
    }

    .row {
        flex-direction: column-reverse;

        @media screen and (min-width:768px) {
            flex-direction: row;
        }
    }

    .benefits__wrapper {
        width: 80%;
        background-color: $color-background;
        margin-top: 2.62rem;
        padding: 3rem 1.37rem 3rem 1.87rem;

        .benefits__img {
            position: relative;
            z-index: 1;

            &:nth-of-type(1)::after {
                content: '';
                position: absolute;
                width: 127px;
                height: 127px;
                bottom: 30%;
                left: 50%;
                border-radius: 3.1rem;
                transform: rotate(45deg);
                z-index: -1;
            }

        }

        .benefits__text {
            text-align: left;

            h3 {
                font-size: 1.87rem;
                font-family: $font-Raleway;
                text-align: left;
                margin-top: 1.62rem;
                margin-bottom: 2.5rem;
                margin-right: 6rem;
                @media screen and (min-width:768px) {
                    margin-top: 0;

                }
            }

            span {
                font-size: 1rem;
            }
        }
    }

    .benefits__wrapper:nth-child(1) {
        @media screen and (min-width:768px) {
            padding-top: 4rem;
            padding-bottom: 4rem;
        }

        .col-md-6:nth-of-type(2) .benefits__img::after {
            background-color: $color-secondary;
        }
    }

    .benefits__wrapper:nth-child(2) {
        @media screen and (min-width:768px) {
            flex-direction: row-reverse;
        }

        img {
            transform: translate(15%, 5%);
        }

        .col-md-6:nth-of-type(2) .benefits__img::after {
            background-color: $color-primary;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            width: 185px;
            height: 185px;
        }
    }

    .benefits__wrapper:nth-child(3) {
                    padding:0;
        @media screen and (min-width:768px) {
            padding: 0;
        }

        .col-md-6:nth-last-of-type(1){
            @media screen and (min-width:768px) {
                padding:0;
                background-color: $color-background-benefits;
            }
        }
        margin-bottom: 2rem;
        flex-direction: column;

        @media screen and (min-width:768px) {
            flex-direction: row;
        }

        .benefits__img {
            @media screen and (min-width:768px) {
               margin-left: 1.5rem;
            }
            img{
                @media screen and (min-width:768px) {
                    width: 25rem;
                }
            }
        }

        .benefits__text{
            padding: 1.5rem;
            @media screen and (min-width:768px) {
                padding: 0 2rem;
            }
        }

        img {
            transform: translateY(15%);
            width: 20rem;
        }

        .col-md-6:nth-of-type(2) .benefits__img::after {
            background-color: $color-third;
            width: 100%;
            height: 100%;
            transform: rotate(0);
            top: 0;
            left: 0;
            border-radius: 0;
        }
    }
}